<template>
    <div class="m-board">
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="(item,index) in dataSource ">
                <img :src="item.img" alt=""/>
            </mt-swipe-item>
        </mt-swipe>
        <board-list></board-list>
    </div>
  
</template>
<script>
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';
import 'mint-ui/lib/style.css'

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);

import BoardList from './BoardList.vue'
Vue.component('board-list', BoardList)

import axios  from 'axios'
export default {
    data(){
        return{
            dataSource:[]
        }
    },
    mounted:function(){
        var that = this;
        axios({
            method:'get',
            url:'http://localhost:9000/swiper'
        })
            .then(function(res) {
               that.dataSource =  that.dataSource.concat(res.data.banner);
        });
    }
}
</script>


